import "./landingpage.css";
import Image from 'next/image';  // Import Image from Next.js (for optimization)
import image2 from '../../images/landingpage/image2.png';  // Import the image
import image3 from '../../images/landingpage/image5.webp'
import image4 from '../../images/landingpage/image6.jpeg'

const LandingPage = () => {
  return (
    <div className="landing-page">
      {/* Top Half with Images */}
      <div className="top-half">
        <Image
          src={image2}  
          alt="Image 2"
        className="image"
        />
        <Image
          src={image3}
          alt="Image 2"
          className="image"
        />
        <Image
          src={image2}
          alt="Image 3"
          className="image"
        />
        <Image
          src={image2}
          alt="Image 4"
          className="image"
        />
      </div>

      {/* Bottom Half with Black Background */}
      <div className="bottom-half">
        <h1 className="heading">
          <span className="letter-s">S</span>
          <span className="letter-o">O</span>
          <span className="letter-k">K</span>
          <span className="letter-k">K</span>
          <span className="letter-a">A</span>
          <span className="letter-i">I</span>
        </h1>
        <p className="subheading">Men Made Better</p>

        {/* Buttons */}
        <div className="button-container">
          <button className="btn login-btn">Login</button>
          <button className="btn signin-btn">Sign In</button>
        </div>
      </div>
    </div>
  );
};

export default LandingPage;
